html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 20px;
  background-color: #cecece;
  color: #000000;
  font-family: "Roboto", sans-serif;
  line-height: 1.3;
}
@media only screen and (min-width: 1000px) {
  body {
    padding: 1.5rem;
  }
}

main {
  padding: 20px;
  background-color: #ffffff;
  border-radius: 15px;
  max-width: 1200px;
  margin: 0 auto;
}
@media only screen and (min-width: 1000px) {
  main {
    display: flex;
    padding: 1.5rem;
  }
}

@media only screen and (min-width: 1000px) {
  .content {
    flex: 3;
  }
}

@media only screen and (min-width: 1000px) {
  aside {
    flex: 1;
  }
}

h1, h2, h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 2rem;
}
@media only screen and (min-width: 1000px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.5rem;
}
@media only screen and (min-width: 1000px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-size: 1.25rem;
}
@media only screen and (min-width: 1000px) {
  h3 {
    font-size: 1.5rem;
  }
}

body {
  background-color: white;
}

.movie-listings {
  text-align: center;
}

.btn-search {
  padding: 10px;
}

form.form-inline > input {
  height: 42px;
}

.img-responsive {
  background-color: #cccccc;
  /* Used if the image is unavailable */
  padding-bottom: 146%;
  background-position: center;
  /* Center the image */
  background-repeat: no-repeat;
  /* Do not repeat the image */
  background-size: cover;
  /* Resize the background image to cover the entire container */
}

.movieLoader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background-image: url("./img/disk.gif");
  background-repeat: no-repeat;
  background-color: #f5f5f5;
  background-position: center;
}

span.yellow {
  background-color: yellow;
  padding: 6px;
}

span.green {
  background-color: green;
  padding: 6px;
}

span.red {
  background-color: red;
  padding: 3px;
}

.loadMore {
  text-align: center;
}

.loadMore span {
  background: #337AB7;
  border-radius: 3px;
  color: #FFFFFA;
  display: inline-block;
  padding: 10px 30px;
  transition: all 0.25s ease-out;
  -webkit-font-smoothing: antialiased;
  text-decoration: auto;
  cursor: pointer;
}

/* Safari */
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/*******************/
@media only screen and (max-width: 767px) {
  form.form-inline > input {
    margin-bottom: 10px;
  }
}